.main {
    max-width: 1520px;
    margin: 0px 30px;
}

@media (min-width: 800px) {
    .main {
        margin-bottom: 30px;
    }
}

.toolbar {
    background: #162138d6;
    text-align: center;
    padding-right: 0px !important;
}

.array-display {
    text-align: center;
    padding-left: 0px !important;
}

@media (max-width: 600px) {
    .toolbar {
        padding-top: 10px;
    }
    .main {
        margin: 0px 0px;
        overflow: auto;
    }
    .array-display {
        padding: 0px !important;
    }
}