  width: 60px;
  height: 30px;
  border: 3px solid #29ffba;
  color: red;
  background-color: white;
}

.tool-btn-stop:disabled {
  background: gray;
  color: rgb(165, 165, 165);
  border: none;
}

.tool-button:hover {
  background: #4cecb9;
  transition: 0.3s;
}

.tool-button:disabled {
  background: gray;
  color: rgb(165, 165, 165);
  transition: 0.3s;
}

